import config from '@/config';

const conf = config.database;

export default {
  db: null,
  open() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(conf.name, conf.version);

      request.onupgradeneeded = (e) => {
        const db = e.target.result;

        if (db.objectStoreNames.contains('users')) {
          db.deleteObjectStore('users');
        }

        db.createObjectStore('users', { keyPath: 'timestamp' });
      };

      request.onsuccess = (e) => {
        this.db = e.target.result;
        resolve();
      };

      request.onerror = reject;
    });
  },
  addUser(user) {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['users'], 'readwrite');
      const store = transaction.objectStore('users');

      const request = store.put(user);

      request.onsuccess = resolve;
      request.onerror = reject;
    });
  },
  deleteUser(user) {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['users'], 'readwrite');
      const store = transaction.objectStore('users');

      const request = store.delete(user.timestamp);

      request.onsuccess = resolve;
      request.onerror = reject;
    });
  },
  fetchUsers() {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['users'], 'readwrite');
      const store = transaction.objectStore('users');

      const keyRange = IDBKeyRange.lowerBound(0);
      const cursorRequest = store.openCursor(keyRange);

      const users = [];

      cursorRequest.onsuccess = (e) => {
        const result = e.target.result;

        if (!!result === false) {
          return;
        }

        users.push(result.value);

        result.continue();
      };

      transaction.oncomplete = () => {
        resolve(users);
      };

      cursorRequest.onerror = reject;
    });
  },
};



