import Vue from 'vue';
import Vuex from 'vuex';
import db from '@/database';
import { chunk, shuffle } from 'lodash';

Vue.use(Vuex);

const ADD_USER = 'ADD_USER';
const REMOVE_USER = 'REMOVE_USER';
const SHUFFLE_USERS = 'SHUFFLE_USERS';

const store = new Vuex.Store({
  state: {
    users: [],
    shuffled: [],
  },
  getters: {
    users(state) {
      return state.users;
    },

    usersCount(state) {
      return state.users.length;
    },

    shuffledUsers(state) {
      return state.shuffled;
    },
  },
  actions: {
    fetchUsers({ commit }) {
      db.fetchUsers().then((users) => {
        users.forEach((user) => {
          commit(ADD_USER, user);
        });
      });
    },

    addUser({ commit }, data) {
      const user = {
        name: data.name,
        timestamp: new Date().getTime(),
      };

      db.addUser(user).then(() => {
        commit(ADD_USER, user);
      });
    },

    removeUser({ commit, state }, user) {
      const index = state.users.findIndex(item => item === user);

      if (index === -1) {
        return;
      }

      db.deleteUser(user).then(() => {
        commit(REMOVE_USER, index);
      });
    },

    shuffleUsers({ commit, state }, size) {
      commit(SHUFFLE_USERS, chunk(
        shuffle(state.users),
        size,
      ));
    },
  },
  mutations: {
    [ADD_USER](state, data) {
      state.users.push(data);
    },

    [REMOVE_USER](state, index) {
      state.users.splice(index, 1);
    },

    [SHUFFLE_USERS](state, data) {
      const newState = state;

      newState.shuffled = data;

      Object.assign(state, newState);
    },
  },
});

export default store;



