import Vue from 'vue';
import Router from 'vue-router';
import TheDashboard from '@/pages/TheDashboard';
import TheShuffle from '@/pages/TheShuffle';
import TheUsers from '@/pages/TheUsers';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: TheDashboard,
    },
    {
      path: '/shuffle',
      name: 'shuffle',
      component: TheShuffle,
    },
    {
      path: '/users',
      name: 'users',
      component: TheUsers,
    },
  ],
});



