<template>
  <v-app v-if="ready">
    <v-toolbar app>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-content>
      <transition
        name="slide-x-transition"
        mode="out-in"
      >
        <router-view />
      </transition>
    </v-content>
    <v-bottom-nav
      :value="true"
      absolute
      app
      fixed
      color="transparent"
    >
      <router-link
        v-for="(link, index) in links"
        :key="index"
        :to="link"
        tag="div"
      >
        <v-btn
          color="teal"
          flat
        >
          <v-icon>{{ link.icon }}</v-icon>
        </v-btn>
      </router-link>
    </v-bottom-nav>
  </v-app>
</template>

<script>
  import db from '@/database';
  import { mapActions } from 'vuex';

  export default {
    data() {
      return {
        clipped: false,
        drawer: false,
        fixed: false,
        links: [
          {
            icon: 'home',
            name: 'dashboard',
          },
          {
            icon: 'autorenew',
            name: 'shuffle',
          },
          {
            icon: 'supervised_user_circle',
            name: 'users',
          },
        ],
        miniVariant: false,
        right: true,
        rightDrawer: false,
        title: 'GroupRandomizer',
        ready: false,
        bottomNav: 'recent',
      };
    },
    name: 'App',
    created() {
      db.open().then(() => {
        this.ready = true;
        this.fetchUsers();
      });
    },
    methods: {
      ...mapActions([
        'fetchUsers',
      ]),

      navigate(link) {
        this.$router.push(link);
      },
    },
};
</script>



