<template>
    <v-text-field
        v-model="form.name"
        append-icon="done"
        solo
        clearable
        label="Додати нового учасника"
        type="text"
        @click:append="confirm"
        @click:clear="clear"
        @keypress.enter="confirm"
    ></v-text-field>
</template>

<script>
  import { mapActions } from 'vuex';

  export default {
    /**
     * Name of the component.
     */
    name: 'UserCreateForm',

    data() {
      return {
        form: {
          name: '',
        },
      };
    },

    methods: {
      ...mapActions([
        'addUser',
      ]),

      clear() {
        this.form.name = '';
      },

      confirm() {
        const name = this.form.name;

        if (!name) {
          return;
        }

        this.addUser({
          name,
        });
        this.clear();
      },
    },
  };
</script>



