<template>
    <v-flex>
        <v-card>
            <v-list>
                <v-list-tile
                    v-for="(user, index) in item"
                    :key="index"
                    avatar
                >
                    <v-list-tile-avatar>
                        <v-icon class="grey lighten-1 white--text">face</v-icon>
                    </v-list-tile-avatar>

                    <v-list-tile-content>
                        <v-list-tile-title>{{ user.name }}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-card>
    </v-flex>
</template>

<script>
  export default {
    /**
     * Name of the component.
     */
    name: 'ShuffleListItem',

    /**
     * Available properties.
     */
    props: {
      /**
       * Item.
       */
      item: {
        type: Array,
        required: true,
      },
    },
  };
</script>



