<template>
    <transition-group
        name="slide-y-transition"
        mode="out-in"
    >
        <shuffle-list-item
            v-for="(item, index) in shuffledUsers"
            :key="index"
            :item="item"
        ></shuffle-list-item>
    </transition-group>
</template>

<script>
  import { mapGetters } from 'vuex';
  import ShuffleListItem from './ShuffleListItem';

  export default {
    /**
     * Name of the component.
     */
    name: 'ShuffleList',

    /**
     * Child components.
     */
    components: {
      ShuffleListItem,
    },

    /**
     * Computed properties.
     */
    computed: {
      /**
       * Vuex.
       */
      ...mapGetters([
        'shuffledUsers',
      ]),
    },

    /**
     * Methods.
     */
    methods: {
    },
  };
</script>



