<template>
    <v-list-tile avatar>
        <v-list-tile-avatar>
            <v-icon class="grey lighten-1 white--text">face</v-icon>
        </v-list-tile-avatar>

        <v-list-tile-content>
            <v-list-tile-title>{{ user.name }}</v-list-tile-title>
        </v-list-tile-content>

        <v-list-tile-action>
            <v-btn
                icon
                ripple
            >
                <v-icon
                    color="grey lighten-1"
                    @click="remove"
                >
                    delete
                </v-icon>
            </v-btn>
        </v-list-tile-action>
    </v-list-tile>
</template>

<script>
  export default {
    /**
     * Name of the component.
     */
    name: 'UsersListItem',

    /**
     * Available properties.
     */
    props: {
      /**
       * User.
       */
      user: {
        type: Object,
        required: true,
      },
    },

    /**
     * Methods.
     */
    methods: {
      /**
       * Remove the user.
       */
      remove() {
        this.$emit('remove', this.user);
      },
    },
  };
</script>



