<template>
    <div>
        <v-card v-if="users.length">
            <v-list>
                <transition-group
                    name="slide-y-transition"
                    mode="out-in"
                >
                    <users-list-item
                        v-for="user in users"
                        :key="user.timestamp"
                        :user="user"
                        @remove="remove"
                    ></users-list-item>
                </transition-group>
            </v-list>
        </v-card>

        <div v-if="!users.length">
            <v-container grid-list-md text-xs-center>
                <v-layout row wrap class="grey--text lighten-1">
                    <v-flex xs12>
                        <v-icon x-large>face</v-icon>
                    </v-flex>
                    <v-flex xs12>
                        Жодного учасника не додано
                    </v-flex>
                </v-layout>
            </v-container>
        </div>
    </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  import UsersListItem from './UsersListItem';

  export default {
    /**
     * Name of the component.
     */
    name: 'UsersList',

    /**
     * Child components.
     */
    components: {
      UsersListItem,
    },

    computed: {
      ...mapGetters([
        'users',
      ]),
    },

    methods: {
      ...mapActions([
        'removeUser',
      ]),
      remove(user) {
        this.removeUser(user);
      },
    },
  };
</script>



