<template>
  <v-container grid-list-md>
    <v-layout column>
      <v-flex>
        <shuffle-control></shuffle-control>
      </v-flex>
      <v-flex>
        <shuffle-list></shuffle-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import ShuffleControl from '@/components/shuffle/ShuffleControl';
  import ShuffleList from '@/components/shuffle/ShuffleList';

  export default {
    /**
     * Component name.
     */
    name: 'TheShuffle',

    /**
     * Child components.
     */
    components: {
      ShuffleControl,
      ShuffleList,
    },
  };
</script>



