<template>
    <div>
        <v-text-field
            v-model="size"
            append-icon="autorenew"
            label="Розбити групу по (наприклад 2)"
            type="number"
            @click:append="shuffle"
            @keypress.enter="shuffle"
            :hint="hint"
            persistent-hint
            solo
        ></v-text-field>
    </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {
    /**
     * Name of the component.
     */
    name: 'ShuffleControl',

    data() {
      return {
        size: null,
      };
    },

    /**
     * Computed properties.
     */
    computed: {
      /**
       * Vuex.
       */
      ...mapGetters([
        'usersCount',
      ]),

      enabled() {
        return !!this.usersCount;
      },

      hint() {
        return `Кількість учасників: ${this.usersCount}`;
      },
    },

    /**
     * Methods.
     */
    methods: {
      /**
       * Vuex.
       */
      ...mapActions([
        'shuffleUsers',
      ]),

      shuffle() {
        if (!this.enabled) {
          return;
        }

        this.shuffleUsers(parseInt(this.size, 10));
      },
    },
  };
</script>



